/*----------------------
     Dividers 
------------------------*/
.divider {
    display: inline-block;
    position: relative;
    width: 50px;
    height: 2px;
    background-color: #e7ebef;

    &-sm {
        width: 20px;
        height: 2px;
    }

    &-xl {
        width: 130px;
    }
}

.divider-primary {
    background-color: $color-primary;
}

.divider-secondary {
    background-color: $color-secondary;
}

.divider-white {
