@include md-screens {
    section {
        padding-top: 60px;
        padding-bottom: 60px;
    }
}

/* Mobile Phones and tablets */
@include xs-sm-screens {
    section {
        padding-top: 50px;
        padding-bottom: 50px;
    }

    .inner-padding {
        padding: 20px !important;
    }

    .text-center-xs-sm {
        text-align: center !important;
    }

    .mt-0-xs-sm {
        margin-top: 0 !important;
    }
